body {
  margin: 0;
  font-family: "Titillium Web", sans-serif;
  overflow-y: hidden;
}

$primary-color: #25142d;
$secondary-color: #7b4397;
$tertiary-color: #dc2430;

$main-gradient-color: linear-gradient(
  108deg,
  rgb(54 28 67) 42%,
  rgb(101 18 23) 100%
);

$light-color: #ffff;

$main-text: #ffff;

$success-color: #78d64c;
$danger-color: #fa5568;
